  max-width: 33.33333333%;
  min-width: 100%;
}

.grid-col_5 {
  flex-basis: 41.66666667%;
  max-width: 41.66666667%;
  min-width: 100%;
}

.grid-col_6 {
  flex-basis: 50%;
  max-width: 50%;
  min-width: 100%;
}

.grid-col_7 {
  flex-basis: 58.33333333%;
  max-width: 58.33333333%;
  min-width: 100%;
}

.grid-col_8 {
  flex-basis: 66.66666667%;
  max-width: 66.66666667%;
  min-width: 100%;
}

.grid-col_9 {
  flex-basis: 75%;
  max-width: 75%;
  min-width: 100%;
}

.grid-col_10 {
  flex-basis: 83.33333333%;
  max-width: 83.33333333%;
  min-width: 100%;
}

.grid-col_11 {
  flex-basis: 91.66666667%;
  max-width: 91.66666667%;
  min-width: 100%;
}

.grid-col_12 {
  flex-basis: 100%;
