img {
  max-width: 100%;
}

.gettinggriddy {
  margin: 18px;
}

.btnrt {
  float: right;
}

.tinytxt {
  font-size: smaller;
}

.btncontainer {
  display: inline-block;
}

.btnlft {
  float: left;
}

.btn {
  background-color: #f56735;
  border-width: 1px 1px 3px 1px;
  border-radius: 4px;
  color: #faf7e2;
  display: inline-block;
  padding: 10px;
  outline: 0;
  cursor: pointer;
  font-size: 3vw;
}

.btn_sizeMin {
  min-height: 50px;
  min-width: 100px;
}

.btn_sizeSm {
  padding: 10px 15px;
  font-size: 3vw;
}

.btn_sizeFull {
  display: block;
  width: 100%;
}

.btn:hover {
  background-color: #cc552a;
  text-decoration: none;
}

.aboutheader {
  text-emphasis: bold;
  font-size: 4.5vw;
}

.dogdescription {
  font-size: 4vw;
}
